import React, { Fragment, useEffect, useRef, useState } from "react"
import { Link, navigate } from "gatsby"

import gLogo from "../images/glogo.png"

export default function NavBar() {
  const [menuOpen, setMenuOpen] = useState(false)
  const menuRef = useRef(null)

  useEffect(() => {
    const handleClick = event => {
      const clickedNode = event.target
      if (
        menuRef.current &&
        !menuRef.current?.contains(clickedNode) &&
        menuOpen
      )
        setMenuOpen(false)
    }

    window.addEventListener("click", handleClick)
    return () => window.removeEventListener("click", handleClick)
  }, [menuRef, menuOpen])

  const Links = () =>
    ["skills", "projects", "about", "contact"].map((item, i) => (
      <li onClick={() => setMenuOpen(false)} key={i}>
        <Link to={`/${item}`}>{item}</Link>
      </li>
    ))

  return (
    <Fragment>
      <div className="menu">
        <img
          src={gLogo}
          alt="G"
          width="50px"
          height="50px"
          onClick={() => navigate("/")}
        />
        <span className="logo-bg" />
        <ul>
          <Links />
        </ul>
      </div>
      <div
        tabIndex="-1"
        role="button"
        className={menuOpen ? "menu-icon active" : "menu-icon"}
        onClick={() => setMenuOpen(!menuOpen)}
      >
        {menuOpen ? (
          <i className="fa fa-times"></i>
        ) : (
          <i className="fa fa-bars"></i>
        )}
      </div>
      <div ref={menuRef}>
        <div className={menuOpen ? "phone-menu active" : "phone-menu"} />
        <div
          className={menuOpen ? "phone-menu-items active" : "phone-menu-items"}
        >
          <ul className="fade-in">
            <Links />
          </ul>
        </div>
      </div>
    </Fragment>
  )
}
