import { graphql, Link } from "gatsby"
import React from "react"
import Layout from "../components/Layout"
import ProjectCard from "../components/ProjectCard"

export default function AllProjects(props) {
  const allProjects = props.data.githubData.data.user.repositories.nodes
  const projects = allProjects
    .filter(project => !!project.homepageUrl && !!project.description)
    .filter(project => !project.name?.includes(".github.io"))

  return (
    <Layout noNav>
      <div className="all-projects-container fade-in">
        <div className="back-btn">
          <Link to="/projects">
            <i className="fa fa-arrow-left"></i>
          </Link>
          All Projects
        </div>
        {projects.map(project => (
          <ProjectCard project={project} />
        ))}
      </div>
    </Layout>
  )
}

export const query = graphql`
  query AllProjectQuery {
    githubData {
      data {
        user {
          repositories {
            nodes {
              id
              name
              url
              description
              homepageUrl
            }
          }
        }
      }
    }
  }
`
